@media all and (min-width:1200px){
    .HomeContainer{
        width: 100vw;
        display: flex;
        justify-content: space-around;
        height: auto;
        line-height: 1;
        text-align: left;
        color:white;
        margin: auto;
        margin-top: 110px;
    }
    .App {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      text-align: center;
      background-image: linear-gradient(to bottom,rgb(0, 0, 0),rgba(0, 0, 0, 0.9));
      width: 100vw;
      height: 100vh;
      z-index: -1;
    }
    .MenuBar{
      display:none;
    }
    .HelloText{
        font-size: 18px;
        color:teal;
        font-weight: 500;
        transition: 1s;
        margin-top: 25px;
    }
    .MyNameText{
        font-size: 80px;
        margin: 0px 0px 0px -5px;
        font-weight: 600;
        letter-spacing: 3;
        transition: 1s;
    }
    .FSWDText{
        font-size: 25px;
        letter-spacing:8.6px;
        font-weight: 600;
        transition: 1s;
    }
    .SubContainer{
        display: flex;
        margin-top: 50px;
        justify-content: space-between;
        transition: 1s;
        margin-left: -3px;
    }
    .Buttons{
        display: flex;
        gap:10px;
        color:white;
        font-size: 15px;
        border-radius: 10px;
        font-weight: 500;
        width: 110px;
        height: 40px;
        cursor: pointer;
    }
    .Avatar{
        width: 550px;
        height: 400px;
        transition: 1s;
        margin-top: -50px;
    }
    .Link{
      color:rgb(160, 160, 160);
      text-decoration: none;
      cursor: pointer;
      transition: 0.3s;
      padding: 2px 8px 5px 8px;
      border-radius: 6px;
    }
    .colorLink{
      color:rgb(248, 191, 4);
      text-decoration: none;
      cursor: pointer;
      padding: 2px 8px 5px 8px;
      transition: 0.3s;
      border-radius: 6px;
    }
    .Link:hover{
      color: white;
    }
    .Container{
      display: flex;
      justify-content: space-between;
      padding: 25px;
      font-size: 22px;
      font-weight: 500;
      height: auto;
      width: 100vw;
      box-sizing: border-box;
    }
    .Logo{
      width: 50px;
      height: 50px;
      margin-left: 30px;
    }
    .NavbarOptions{
      color: white;
      display: flex;
      justify-content: space-between;
      gap:55px;
      margin-right: 10px;
    }
}

