@media (max-width:912px) and (min-width:450px){
  .MenuBar{
    position: absolute;
    right:15px;
    top:15px;
    font-size: 20px;
    color:white;
    cursor: pointer;
    z-index: 2;
    transition: 2s;
    transform: rotate(180deg);
    box-sizing: border-box;
  }
  .Logo{
    width: 40px;
    height: 40px;
  }
  .LogoDiv{
    padding: 13px 0 0 15px;
    position: static;
  }
  .Container{
    display: flex;
    justify-content: space-between;
    padding: 0px;
    font-size: 22px;
    font-weight: 500;
    height: 100%;
    width: 100vw;
    box-sizing: border-box;
  }
  .NavbarOptions{
    display: none;
  }
  .NavbarOptionsAfter{
    position: relative;
    width: 50vw;
    color: white;
    display: flex;
    background-color: black;
    flex-direction: column;
    justify-content: space-evenly;
    height: 50vh;
    z-index: 1;
    box-sizing: border-box;
  }
  .Link{
    color:rgb(160, 160, 160);
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;
    padding: 5px 8px 8px 8px;
    font-size: 17px;
    box-sizing: border-box;
  }
  .colorLink{
    color:rgb(248, 191, 4);
    text-decoration: none;
    cursor: pointer;
    padding: 5px 8px 8px 8px;
    transition: 0.3s;
    border-radius: 6px;
    font-size: 20px;
    box-sizing: border-box;
  }
  .Link:hover{
    color: white;
    background-color: rgb(19, 19, 19);
  }
  .Buttons{
      display: flex;
      gap:4px;
      color:white;
      font-size: 13px;
      border-radius: 10px;
      font-weight: 400;
      width: 340px;
      height: 35px;
      border: none;
      cursor: pointer;
      margin-left: -4px;
  }
  .SubContainer{
      display: flex;
      flex-direction: column;
      gap:10px;
      margin-top: 50px;
      justify-content: space-between;
      transition: 1s;
  }
  .Container{
    width: 100%;
    position: absolute;
    top:0px;
    left:0px;
    transition: 0.5s;
    box-sizing: border-box;
  }
  .HelloText{
      font-size: 18px;
      margin-top: 30px;
      color:white
  }
  .MyNameText{
      font-size: 45px;
      margin: 0px 0px 0px 0px;
      font-weight: 500;
      letter-spacing:5px;
      color:teal;
      transition: 1s;
  }
  .FSWDText{
      font-size: 20px;
      letter-spacing:4.5px;
      font-weight: 400;
      transition: 1s;
      margin-left: -2px;
  }
  .Avatar{
      width: 420px;
      height: 310px;
      transition: 1s;
      margin-top: 90px;
  }
  .App{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    background-image: linear-gradient(to bottom,rgb(0, 0, 0),rgba(0, 0, 0, 0.9));
    width: 100vw;
    height: 110vh;
    z-index: -1;
  }
  .HomeContainer{
      width: 100vw;
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-around;
      align-items: center;
      gap:30px;
      height: auto;
      line-height: 1;
      color:white;
      margin: auto;
      padding: 20px;
      box-sizing: border-box;
  }
}



